body {
  background-color: #001219;
  font-family: Calibre, Inter, "San Francisco", "SF Pro Text", -apple-system,
    system-ui, sans-serif;
  color: #f7ede2;
}

#body-content {
  width: 100%;
  padding: 0px 150px;
  position: absolute;
  z-index: 1;
}

.sections {
  margin: 0px auto;
  padding: 100px 0px;
}

.section-heading {
  display: flex;
  align-items: center;
  position: relative;
  margin: 10px 0px 40px;
  width: 100%;
  font-size: clamp(26px, 5vw, 32px);
  white-space: nowrap;
  color: #ddb892;
}

.section-heading::after {
  content: "";
  display: block;
  position: relative;
  width: 300px;
  height: 1px;
  margin-left: 20px;
  background-color: #ddb892;
}

.footer {
  text-align: center;
  min-height: 70px;
  padding: 30px;
  color: #ddb892;
}

.my-credit {
  margin: 0px;
  font-size: 15px;
}

.credits {
  font-size: 13px;
  margin: 10px 0px 0px;
}

.credits a {
  color: #ddb892;
  text-decoration: none;
}

.credits a:hover {
  color: #f7ede2;
}

.content-container-button {
  display: none !important;
}

/* Screen width 991px max */
@media screen and (max-width: 991px) {
  #body-content {
    padding: 0px 50px;
  }

  .content-container-left {
    display: none;
  }

  .content-container-right {
    display: none;
  }

  .content-container-button {
    display: block !important;
  }

  .inner-content {
    grid-gap: 0px !important;
    gap: 0px !important;
  }

  .profile {
    flex: none;
    width: 100%;
    margin: 50px auto 0px;
  }

  .profile img {
    max-width: 300px;
    max-height: 300px;

    display: block;
    margin-left: auto;
    margin-right: auto;
  }

  .container.project-container {
    max-width: 970px;
    margin: 0px 20px 0px 20px;
  }
}

/* Screen width 768px max */
@media screen and (max-width: 768px) {
  .col {
    flex: none;
  }

  .project-link {
    margin-top: 30px;
    text-align: center !important;
  }

  .container.project-container {
    margin: 0px;
  }

  .project-image {
    margin-bottom: 30px;
    width: 700px !important;
    height: 300px !important;
  }
}

/* Screen width 500px max */
@media screen and (max-width: 500px) {
  #body-content {
    padding: 0px 20px;
  }

  .navbar {
    padding: 20px 20px !important;
  }

  .project-image {
    margin-bottom: 30px;
    width: 700px !important;
    height: 200px !important;
  }

  .project-description {
    padding: 20px !important;
  }
}

/* Screen width 300px max */
@media screen and (max-width: 375px) {
  #body-content {
    padding: 0px 10px;
    width: 340px;
  }

  .navbar {
    padding: 20px 20px !important;
  }

  .inner-content {
    padding: 0px 12px;
  }

  .col {
    padding: 0px;
  }

  .project-description {
    padding: 15px !important;
  }

  .project-description p {
    margin: 0px !important;
  }

  .tech-list li {
    margin: 0px 15px 5px 0px !important;
  }
}

.particle-box {
  position: absolute;
  width: 100%;
  height: 100vh;
  top: 0;
  left: 0;
}

.navbar-dark .navbar-nav .nav-link {
  color: #e9d8a6;
  margin: 0 1.5rem;
}

.navbar-dark .navbar-nav .nav-link:hover {
  color: #ddb892;
}

.btn {
  color: #e9d8a6;
  border-color: #e9d8a6;
  margin: 0 1.5rem;
  padding: 8px;
}

.btn:hover {
  color: #f0efeb;
  background-color: #ddb892;
  border-color: #ddb892;
}

.navbar {
  padding: 20px 50px;
  width: 100%;
  background-color: #001219 !important;
  z-index: 4;
}

.container-fuid {
  height: 100px;
  background-color: #001219;
}

/* Social media for left side */
.content-container-left {
  width: 40px;
  position: fixed;
  bottom: 0px;
  left: 20px;
  right: auto;
  z-index: 3;
}

/* Add vertical line after icons */
.content-container-left::after {
  content: "";
  display: block;
  width: 2px;
  height: 100px;
  margin: 0px auto;
  background-color: #ddb892;
}

.social-content-left {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 0px;
  padding: 0px;
  list-style: none;
}

.social-content-left li:last-of-type {
  margin-bottom: 20px;
}

/* Social media for footer */
.content-container-button {
  display: block;
  width: 100%;
  max-width: 270px;
  margin: 0px auto 10px;
}

.social-content-button {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0px;
  margin: 0px;
  list-style: none;
}

.link {
  padding: 15px;
  display: inline-block;
  text-decoration: none;
}

.icons {
  width: 25px;
  height: 25px;
  -webkit-filter: invert(100%) sepia(20%) saturate(5705%) hue-rotate(305deg)
    brightness(92%) contrast(88%);
          filter: invert(100%) sepia(20%) saturate(5705%) hue-rotate(305deg)
    brightness(92%) contrast(88%);
}

.icons:hover {
  -webkit-filter: invert(100%) sepia(0%) saturate(7307%) hue-rotate(116deg)
    brightness(85%) contrast(124%);
          filter: invert(100%) sepia(0%) saturate(7307%) hue-rotate(116deg)
    brightness(85%) contrast(124%);
}

.content-container-right {
  width: 40px;
  position: fixed;
  bottom: 0px;
  left: auto;
  right: 20px;
  z-index: 10;
}

/* Add vertical line after icons */
.content-container-right::after {
  content: "";
  display: block;
  width: 2px;
  height: 100px;
  margin: 0px auto;
  background-color: #ddb892;
}

.email-content {
  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative;
}

.email {
  margin: 20px auto;
  padding: 10px;
  font-size: 12px;
  line-height: 18px;
  letter-spacing: 0.1em;
  -webkit-writing-mode: vertical-rl;
      -ms-writing-mode: tb-rl;
          writing-mode: vertical-rl;
  text-decoration: none;
  position: relative;
  color: #ddb892;
}

.email:hover {
  color: #f0efeb;
}

#intro-about-me {
  display: flex;
  justify-content: center;
  flex-direction: column;
  min-height: 100vh;
  padding: 0px;
}

.about-container {
  max-width: 1000px;
}

.greetings {
  color: #e9d8a6;
  line-height: 1.1;
  font-size: 20px;
  letter-spacing: 2px;
  font-weight: 400;
  margin: 0px 0px 30px 4px;
}

.my-name {
  color: #f7ede2;
  margin: 0px;
  font-size: clamp(40px, 7vw, 80px);
}

.small-fact {
  color: #ddb892;
  font-size: clamp(40px, 7vw, 80px);
  margin: 10px 0px 0px 0px;
  line-height: 0.9;
}

.big-fact {
  margin: 20px 0px 0px;
  color: #ddb892;
  font-size: 20px;
  max-width: 540px;
}

/* #about-me {
  margin: 0px auto;
  padding: 100px 0px;
} */

.inner-content {
  grid-gap: 50px;
  gap: 50px;
}

.technology-message {
  color: #ddb892;
  font-size: 18px;
  font-weight: bold;
  margin: 0px;
}

.languages-ul {
  display: grid;
  grid-template-columns: repeat(2, minmax(140px, 200px));
  grid-gap: 0px 10px;
  gap: 0px 10px;
  padding: 0px;
  margin: 20px 0px 0px;
  overflow: hidden;
  list-style: none;
}

.programing-languages h4 {
  font-size: 15px;
}

.frameworks-tools h4 {
  font-size: 15px;
}

.profile-image {
  -webkit-filter: drop-shadow(0 0 1rem #ddb892);
          filter: drop-shadow(0 0 1rem #ddb892);
}

.technology-list {
  position: relative;
  margin-bottom: 10px;
  padding-left: 20px;
  font-size: 15px;
  font-weight: bold;
}

.technology-list::before {
  content: "\25B6";
  position: absolute;
  left: 0px;
}


.project-detail {
  margin: 0px 0px 100px;
  width: 100%;
}

.project-description {
  padding: 25px;
  background-color: #112240;
  box-shadow: 15px 18px 30px -20px rgba(66, 121, 204, 0.7);
  border-radius: 4px;
}

.featured-title {
  font-size: 13px;
  color: #e9d8a6;
  margin: 10px 0px;
}

.project-name {
  margin: 0px 0px 20px;
}

.project-name a {
  font-size: 28px;
  color: #f7ede2;
  text-decoration: none;
}

.project-name a:hover {
  color: #f1e5bb;
}

.tech-list {
  display: flex;
  margin: 25px 0px 10px;
  padding: 0px;
  list-style: none;
}

.tech-list li {
  margin: 0px 20px 5px 0px;
}

.project-link {
  margin-left: -10px;
}

.project-link a {
  margin: 10px;
  padding: 12px 16px;
}

.project-image {
  width: 300px;
  height: 400px;
}

.project-image a img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 10px;
  box-shadow: 15px 18px 30px -20px #ddb892;
}

.contact {
  max-width: 600px;
  margin: 0px auto 100px;
  text-align: center;
  padding: 100px 0px;
}

.contact-title {
  font-size: clamp(40px, 5vw, 60px);
  margin: 0px 0px 10px;
}

.contact-description {
  margin: 0px;
}

.contact-button {
  margin: 50px 0px 0px;
  padding: 12px 16px;
}

